import { useCallback, useState } from 'react'
import { RoutePath } from 'shared/config/routeConfig'
import { classNames } from 'shared/lib/classNames/classNames'
import { AppLink, AppLinkTheme } from 'shared/ui/AppLink/AppLink'
import { Button, ButtonSize, ButtonTheme } from 'shared/ui/Button/Button'
import { LangSwitcher } from 'widgets/ui/LangSwitcher'
import { ThemeSwitcher } from 'widgets/ui/ThemeSwitcher'
import styles from './Sidebar.module.scss'
import HomeIcon from 'shared/assets/icons/home_icon.svg'
import AboutIcon from 'shared/assets/icons/about_icon.svg'

interface SidebarProps {
  className?: string
}

export const Sidebar: React.FC<SidebarProps> = ({ className = '' }) => {
  const [isCollapsed, setIsCollapsed] = useState<boolean>(false)

  const toggleCollapsed = useCallback(() => {
    setIsCollapsed(prevValue => !prevValue)
  }, [])

  return (
    <div
      data-testid='sidebar'
      className={classNames([styles.sidebar, className], { [styles.collapsed]: isCollapsed })}
    >
      <Button
        data-testid='sidebar-toggle'
        onClick={toggleCollapsed}
        className={styles.collapseButton}
        theme={ButtonTheme.BACKGROUND_INVERTED}
        size={ButtonSize.L}
        square
      >
        {isCollapsed ? '>' : '<'}
      </Button>
      <div className={styles.items}>
        <AppLink
          to={RoutePath.main}
          className={styles.item}
          theme={AppLinkTheme.INVERTED}
        >
          <HomeIcon className={styles.icon} />
          <span className={styles.link}>Main</span>
        </AppLink>
        <AppLink
          to={RoutePath.about}
          className={styles.item}
          theme={AppLinkTheme.INVERTED}
        >
          <AboutIcon className={styles.icon} />
          <span className={styles.link}>About</span>
        </AppLink>
      </div>
      <div className={styles.switchers}>
        <ThemeSwitcher />
        <LangSwitcher className={styles.lang} short={isCollapsed} />
      </div>
    </div>
  )
}
